<template>
  <footer>
    Presented by <a href="https://github.com/cogons/issvue">issvue</a> & <a :href="url">{{owner}}</a>
  </footer>
</template>

<script>
import config from '../config'
export default {
  name: 'main-footer',
  data: function() {
    return {
      owner: config.repo.user,
      url: 'https://github.com/' + config.repo.user
    }
  }
};
</script>
<style scoped>
footer {
  text-align: center;
  font-size: 11px;
  padding: 30px 0;
  color: #999;
}

footer a {
  font-weight: bold;
  color: #999;
}
</style>


